import { Controller } from '@ali/4ever-cangjie';

export default function isAtEndOfBlock(controller: Controller) {
  const { selection, startBlock, document } = controller.value;
  if (!selection || !startBlock) {
    return false;
  }

  const start = selection.getStart(document);
  return selection.isCollapsed && start.isAtEndOfNode(startBlock);
}
